import { Component, OnInit } from '@angular/core';
import * as Chart from 'chart.js';
import { DeviceChannelControllerService, ResponseDeviceChannelData } from 'src/app/rest';
import * as moment from 'moment';
import { forkJoin, of, Observable, Subscription } from 'rxjs';
import { AuthService } from 'src/app/auth/auth.service';
import { DataService } from '../../../../services/data-service';
import {rotate270} from '2d-array-rotation';
import * as $ from 'jquery';

@Component({
  selector: 'app-pie-chart',
  templateUrl: './pie-chart.component.html',
  styleUrls: ['./pie-chart.component.css']
})
export class PieChartComponent implements OnInit {
  error: string = null;
  isLoading = false;
  title = 'The first google chart';
  type = 'LineChart';
  canvas: any;
  ctx: any;
  myChart: any;
  dataArray: Array<object> = [];
  randomColors: string[] = [];
  allData = {labels: [], datasets: []};
  today = new Date();
  clicked = false;
  fromDate: Date = new Date();
  allRows = [];
  period: number;
  allChannels = [];
  subscription: Subscription;
  // options = {
  //   colors: this.randomColors,
  //   legend: { position: 'top' },
  //   explorer: {
  //     actions: ['dragToZoom', 'rightClickToReset'],
  //     axis: 'horizontal',
  //     keepInBounds: true
  // }
  // };

  public deviceArray = [
    {deviceId: 101, channelId: 1665},
    {deviceId: 101, channelId: 1666},
    {deviceId: 102, channelId: 1695},
    {deviceId: 102, channelId: 1696},
    {deviceId: 103, channelId: 1725},
    {deviceId: 103, channelId: 1726},
    {deviceId: 104, channelId: 1755},
    {deviceId: 104, channelId: 1756},
    {deviceId: 105, channelId: 1785},
    {deviceId: 105, channelId: 1786},
    {deviceId: 106, channelId: 1815},
    {deviceId: 106, channelId: 1816},
  ];

  public selection = 'day';
  public depth = 1;
  public offset = 0;
  public buttonGroup = [
    {text: 'Plant', isClicked: false},
    {text: 'Inverters', isClicked: true}
  ];
  public buttonGroup2 = [
    {text: 'day', isClicked: true},
    {text: 'month', isClicked: false},
    {text: 'year', isClicked: false}
  ];
  public selectedDeviceObj = this.buttonGroup2[0];
  public selected2 = this.buttonGroup[1];

  constructor(
    private deviceChannelControllerService: DeviceChannelControllerService,
    public authService: AuthService,
    private service: DataService
  ) { }

  ngOnInit() {
    this.subscription = this.service.$fromDate
    .subscribe( (data) => {
      this.fromDate = data;
      this.onRequest();
    }, error => {
      console.log(error);
    });
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  onRequest() {
    const today = this.fromDate;
    const tomorrow = moment(today).add(1, 'days').toDate();

    const allRequests: Observable<ResponseDeviceChannelData>[] = [];
    const allResponses: ResponseDeviceChannelData[] = [];
    for (const i in this.deviceArray) {
      allRequests
      .push(this.deviceChannelControllerService.deviceChannelControllerFindByPeriod(this.deviceArray[i].deviceId,
        + this.deviceArray[i].channelId, today.toString(), tomorrow.toString(), 0, 200, 'none', 'none'));
    }

    forkJoin(allRequests)
    .subscribe
    (responses => {
      allResponses.push(...responses);
      for (let countResponse in responses) {
        const color = (this.getRandomColor());
        this.randomColors.push(color);
      }
      this.addValue(responses);
    },
    errorMessage => {
      // this.authService.logout();
      console.log(errorMessage);
    });
  }

  getRandomColor() {
    const letters = '0123456789ABCDEF'.split('');
    let color = '#';
    for (let i = 0; i < 6; i++ ) {
        color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
  }

  setActive(button) {
    for (const but of this.buttonGroup) {
      but.isClicked = false;
    }
    button.isClicked = true;
    this.onRequest();
    // if (button.text === 'Plant' && button.isClicked) {
    //   this.createPlant();
    // } else if (button.text === 'Inverters' && button.isClicked) {
    //   this.createInverter();
    // }
  }

  onSelect(selectionObj) {
    for (const but of this.buttonGroup2) {
      but.isClicked = false;
    }
    selectionObj.isClicked = true;
    this.selection = selectionObj.text;
    this.onRequest();
  }

  createPlant() {
    google.charts.load('current', { packages: ['corechart'] });
    const testparam = this.allRows;
    const randomColor = this.randomColors;
    google.charts.setOnLoadCallback(drawThisChart);
    function drawThisChart() {
      const dataRow = [];
      testparam.forEach(element => {
        const xValues = [];
        element.forEach(x => {
          xValues.push(x.y);
        });
        const sumValues = xValues.reduce(function(a, b) {
          return a + b;
      }, 0);
        dataRow.push([element[0].x, sumValues]);
      });
      const firstRow = ['timestamp', 'Plant'];
      dataRow.splice(0, 0, firstRow);
      const newData = google.visualization.arrayToDataTable(dataRow);
      const chart = new google.visualization.LineChart(document.getElementById('linechart'));
      chart.draw(newData,  {
        colors: randomColor,
        legend: { position: 'top' },
        explorer: {
          actions: ['dragToZoom', 'rightClickToReset'],
          axis: 'horizontal',
          keepInBounds: true
      }});
    }
    $(window).resize(function() {
      drawThisChart();
    });
  }

  findMinMaxDate(data: ResponseDeviceChannelData[]) {
    let minDate: moment.Moment  = moment(data[0].dataFloat[data[0].dataFloat.length - 1].timestamp);
    let minDateFormat: string = minDate.format('YYYY-MMM-DD HH:mm');
    let maxDate: moment.Moment = moment(data[0].dataFloat[0].timestamp);
    let maxDateFormat: string = maxDate.format('YYYY-MMM-DD HH:mm');
    for (const channel of data) {
      if (maxDate.isBefore(channel.dataFloat[0].timestamp)) {
        maxDate = moment(channel.dataFloat[0].timestamp);
        maxDateFormat = maxDate.format('YYYY-MMM-DD HH:mm');
      }
      if (minDate.isAfter(channel.dataFloat[channel.dataFloat.length - 1].timestamp)) {
        minDate = moment(channel.dataFloat[channel.dataFloat.length - 1].timestamp);
        minDateFormat = minDate.format('YYYY-MMM-DD HH:mm');
      }
    }
    return {maxDate, minDate, maxDateFormat, minDateFormat};
  }

  addValue(data: ResponseDeviceChannelData[]) {
    let counter = 0;
    const endarray = [];
    const arrayDatasets = [];
    this.allChannels = [];
    const arrayLabel = [];
    const {maxDate, minDate, maxDateFormat, minDateFormat} = this.findMinMaxDate(data);
    const allDates = [];

    for (const channel of data) {
      this.allChannels.push(channel.channelType.name);
      let tempMinDate = moment(channel.dataFloat[channel.dataFloat.length - 1].timestamp);
      const dataXY = [];
      let flagDate = maxDate;
      let xy = {x: null, y: null};
      channel.dataFloat.forEach(element => {
        const {period, measurement, timestamp} = element;
        this.period = period;
        const dateString = moment(timestamp).format('YYYY-MMM-DD HH:mm');

        while (moment(timestamp).isBefore(flagDate)) {
          xy = {
            x: flagDate.format('YYYY-MMM-DD HH:mm'),
            y: null
          };
          flagDate = moment(flagDate).subtract(this.period, 'seconds');
          dataXY.push(xy);
        }

        if (moment(timestamp).isSame(flagDate)) {
          xy = {
            x: dateString,
            y: measurement
          };
          flagDate = moment(flagDate).subtract(this.period, 'seconds');
          dataXY.push(xy);
        }
        arrayLabel.push(dateString);
      });

      counter += 1;
      while (minDate.isBefore(tempMinDate)) {
      tempMinDate = moment(tempMinDate).subtract(this.period, 'seconds');
      xy = {
        x: tempMinDate.format('YYYY-MMM-DD HH:mm'),
        y: null
      };
      dataXY.push(xy);
      }
      allDates.push(dataXY);
    }

    this.allRows = rotate270(allDates);
    for (const button of this.buttonGroup) {
      if (button.text === 'Plant' && button.isClicked) {
        this.createPlant();
      } else if (button.text === 'Inverters' && button.isClicked) {
        this.createInverter();
      }
    }
  }

  createInverter() {
    google.charts.load('current', { packages: ['corechart'] });
    const testparam = this.allRows;
    const channels = this.allChannels;
    const randomColor = this.randomColors;

    // const options = this.options;
    google.charts.setOnLoadCallback(drawThisChart);
    function drawThisChart() {
      const dataRow = [];
      testparam.forEach(element => {
        const xValues = [];
        element.forEach(x => {
          xValues.push(x.y);
        });
        dataRow.push([element[0].x, ...xValues]);
      });
      const firstRow = ['timestamp'];
      channels.forEach(channel => {
        firstRow.push(channel);
      });
      dataRow.splice(0, 0, firstRow);
      const newData = google.visualization.arrayToDataTable(dataRow);
      const chart = new google.visualization.LineChart(document.getElementById('linechart'));
      chart.draw(newData,  {
        colors: randomColor,
        legend: { position: 'top' },
        explorer: {
          actions: ['dragToZoom', 'rightClickToReset'],
          axis: 'horizontal',
          keepInBounds: true
      }});
    }
    $(window).resize(function() {
      drawThisChart();
    });
  }
}


