import { NgModule } from '@angular/core';
import { Routes, RouterModule} from '@angular/router';
import { AssetGroupsComponent } from './asset-groups/asset-groups.component';
import { AssetsComponent } from './assets/assets.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { EventsComponent } from './events/events.component';
import { AuthComponent } from './auth/auth.component';
import { AuthGuardService } from './auth/auth-guard.service';
import { MissionControllerComponent } from './mission-controller/mission-controller.component';
import { ThermographyComponent } from './common/components/thermography/thermography.component';
import { ImageViewComponent } from './common/components/image-view/image-view.component';
import { DroneMapComponent } from './common/components/drone-map/drone-map.component';


const routes: Routes = [
  {path: 'assetGroup/:id/asset/:id/:id/thermography', component: ThermographyComponent, canActivate: [AuthGuardService]},
  {path: 'assetGroup/:id/asset/:id/dashboard', component: DashboardComponent, canActivate: [AuthGuardService]},
  {path: 'assetGroup/:id/asset/:id/events', component: EventsComponent, canActivate: [AuthGuardService]},
  {path: 'assetGroup/:id/asset/:id/missionController', component:  MissionControllerComponent, canActivate: [AuthGuardService]},
  {path: 'assetGroup/:id/asset/:id/imageView', component: ImageViewComponent, canActivate: [AuthGuardService]},
  {path: 'assetGroup/:id/asset/:id/droneMap', component: DroneMapComponent, canActivate: [AuthGuardService]},
  // {path: 'assetGroup/:id/asset/:id/devices', component: AssetDevicesComponent, canActivate: [AuthGuardService]},
  {path: 'assetGroup/:id', component: AssetsComponent, canActivate: [AuthGuardService]},
  // {path: 'users/:id/assets', component: UserAssetComponent, canActivate: [AuthGuardService]},
  // {path: 'devices', component: DeviceComponent, canActivate: [AuthGuardService]},
  {path: 'assetGroups', component: AssetGroupsComponent, canActivate: [AuthGuardService]},
  // {path: 'adminDashboard', component: AdminDashboardComponent, canActivate: [AuthGuardService]},
  // {path: 'dataLoggers', component: DataLoggersComponent, canActivate: [AuthGuardService]},
  // {path: 'users', component: UsersComponent, canActivate: [AuthGuardService]},
  {path: 'auth', component: AuthComponent},
  {path: '', redirectTo: '/assetGroups', pathMatch: 'full'},
  {path: '**', redirectTo: '/assetGroups', pathMatch: 'full'},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
